<template>
  <div id="barMain"></div>
</template>
<script>
export default {
  data() {
    return {};
  },
  methods: {
    fillChart1(data1, data2, data3, data4) {
      data2.forEach((item) => {
        item["smooth"] = true;
        item['type'] =  'bar';
        item["areaStyle"] = {
          color: new this.$echarts.graphic.LinearGradient(0, 0, 0, 1, [
            {
              offset: 0,
              color: "rgba(76, 101, 179, 0.55)",
            },
            {
              offset: 1,
              color: "rgba(0, 157, 255, 0.55)",
            },
          ]),
        };
      });

      let chart_projectLoad = this.$echarts.init(
        document.getElementById("barMain")
      );
      chart_projectLoad.clear();
      console.log(data2);
      var option = {
        title: {
          text: "",
        },
        tooltip: {
          trigger: "axis",
        },
        legend: {
          data: data3,
          selected: data4,
          type: "scroll",
          orient: "vertical",
          left: "0%",
          textStyle: {
            color: "#fff",
          },
        },
        grid: {
          left: "30%",
          right: "1%",
          bottom: "2%",
          top: "3%",
          containLabel: true,
        },
        toolbox: {
          feature: {
            saveAsImage: {
              show: false,
            },
          },
        },
        xAxis: {
          type: "category",
          boundaryGap: true,
          data: data1,
          axisLabel: {
            show: true,
            textStyle: {
              color: "#ffffff",
            },
          },
        },
        yAxis: {
          type: "value",
          axisLabel: {
            show: true,
            textStyle: {
              color: "#ffffff",
            },
          },
        },
        series: data2,
      };

      chart_projectLoad.setOption(option);
    },
  },
};
</script>
<style scoped>
#barMain {
  width: 100%;
  height: 100%;
}
</style>