<template>
  <div class="energyTrendAnalysis">
    <div class="right border-box">
      <div class="search chartSearch">
        <div>
          <el-date-picker
            v-model="date1"
            type="date"
            placeholder=""
            value-format="yyyy-MM-dd"
            :clearable="false"
          >
          </el-date-picker>
        </div>
        <div style="color: #00b4fe">至</div>
        <div>
          <el-date-picker
            v-model="date2"
            type="date"
            placeholder=""
            value-format="yyyy-MM-dd"
            :clearable="false"
          >
          </el-date-picker>
        </div>
        <div>
          <span>电站</span>
          <el-select
            v-model="elecstationVal"
            placeholder=""
            @change="elecEqmListFun"
          >
            <el-option
              v-for="item in elecstationOptions"
              :key="item.elecStationId"
              :label="item.elecStationName"
              :value="item.elecStationId"
            >
            </el-option>
          </el-select>
        </div>
        <div>
          <span>设备名称</span>
          <el-select v-model="elecEqmVal" placeholder="">
            <el-option
              v-for="item in elecEqmOptions"
              :key="item.elecEqmId"
              :label="item.elecEqmName"
              :value="item.elecEqmId"
            >
            </el-option>
          </el-select>
        </div>
        <div class="tabAll">
          <div
            v-for="(item, index) in timeTab"
            :key="index"
            :class="{ isActive: index == tTIndex }"
            @click="changeTimeTab(index)"
          >
            {{ item }}
          </div>
        </div>
        <button class="searchBth" @click="ylchartsFun()">查询</button>
      </div>
      <div class="tabel">
        <el-table :data="tableData" style="width: 100%" height="250">
          <el-table-column prop="DateStr1" label="日期"> </el-table-column>
          <el-table-column prop="AreaName1" label="园区名称"> </el-table-column>
          <el-table-column prop="CompanyName1" label="企业名称">
          </el-table-column>
          <el-table-column prop="ElecStationName1" label="电站名称">
          </el-table-column>
          <el-table-column prop="ElecEqmName1" label="设备名称">
          </el-table-column>
          <el-table-column prop="Demand1" label="用量kWh"> </el-table-column>
        </el-table>
      </div>
      <div class="border-box chart" style="flex-grow: 1">
        <div style="flex-grow: 1; display: flex;justify-content: space-between;">
          <div style="width: 48%; height: 100%">
            <ylChart ref="yl"></ylChart>
          </div>
          <div style="width: 48%; height: 100%">
            <ylBarChart ref="ylbar"></ylBarChart>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import ylChart from "./components/totalReportChart/ylChart";
import ylBarChart from "./components/totalReportChart/ylBarChart";
import { elecstation, elecEqmList, ylcharts } from "@/network/totalReport";
export default {
  components: {
    ylChart,ylBarChart
  },
  data() {
    return {
      timeTab: ["年", "月", "日"],
      tTIndex: 2,
      elecstationOptions: [],
      elecstationVal: "",
      elecEqmOptions: [],
      elecEqmVal: "",
      date1: "",
      date2: "",
      tableData: [],
    };
  },
  mounted() {
    this.date2 = this.date1 =
      this.$getDate().year +
      "-" +
      this.$getDate().month +
      "-" +
      this.$getDate().day;
    this.elecstationFun();
  },
  methods: {
    changeTimeTab(index) {
      this.tTIndex = index;
      this.ylchartsFun();
    },
    elecstationFun() {
      elecstation()
        .then((res) => {
          const { elecstation, errmsg, errcode } = res;
          if (errcode == 0) {
            this.elecstationOptions = elecstation;
          }
        })
        .catch((err) => {});
    },
    elecEqmListFun() {
      this.elecEqmVal = "";
      elecEqmList(this.elecstationVal)
        .then((res) => {
          const { content, errmsg, errcode } = res;
          if (errcode == 0) {
            this.elecEqmOptions = content;
          }
        })
        .catch((err) => {});
    },
    ylchartsFun() {
      ylcharts(
        this.tTIndex,
        this.date1,
        this.elecEqmVal,
        this.elecstationVal,
        this.date2
      )
        .then((res) => {
          const { content, errmsg, errcode } = res;
          if (errcode == 0) {
            console.log(content);
            this.tableData = content.line;

            this.$refs.yl.fillChart1(
              content.data1,
              content.data2,
              content.data3,
              content.data4
            );
            this.$refs.ylbar.fillChart1(
              content.data1,
              content.data2,
              content.data3,
              content.data4
            );
          }
        })
        .catch((err) => {});
    },
  },
};
</script>
<style scoped lang="less">
.energyTrendAnalysis {
  background-image: url(@/assets/BG2.png);
  background-size: 100% 100%;
  display: flex;
  padding: 0.2rem;
  box-sizing: border-box;
  background-color: rgba(0, 15, 39, 1);
  width: 100%;
}
.border-box {
  border: 1px solid rgba(34, 175, 237, 0.5);
  border-radius: 0.05rem;
  background: rgba(0, 55, 146, 0.2);
  display: flex;
  flex-direction: column;
}
.left {
  width: 25%;
}
.right {
  width: 100%;
  margin-left: 0.2rem;
  display: flex;
  flex-direction: column;
  padding: 0.2rem 0.2rem;
}
.search {
  display: flex;
  flex-wrap: wrap;
  > div {
    margin-right: 0.2rem;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-wrap: nowrap;
    margin-bottom: 0.2rem;
  }
  span {
    color: #00b4fe;
    margin-right: 0.2rem;
    white-space: nowrap;
  }
}
.tabel {
  margin: 0rem 0 0;
}
.chart {
  flex-grow: 1;
}
/deep/ .el-range-editor .el-range-input {
  background: transparent;
  color: #fff;
}
/deep/ .el-date-editor.el-input,
/deep/ .el-date-editor.el-input__inner {
  width: 150px;
}
/deep/ .el-input__inner {
  width: 150px;
  background-color: rgba(0, 55, 146, 0.2);
  border: 1px solid rgba(34, 175, 237, 0.5);
}
/deep/ .el-date-editor .el-range-separator {
  color: #fff;
}
/**表格**/
/deep/ .el-table,
/deep/ .el-table__expanded-cell,
/deep/ .el-table tr {
  background-color: transparent;
  color: #fff;
}
/deep/ .el-table th.el-table__cell {
  background-color: rgba(0, 90, 228, 0.5);
  color: #fff;
}
/deep/ .el-table td.el-table__cell,
/deep/ .el-table th.el-table__cell.is-leaf {
  border-bottom: none;
  text-align: center;
}
/deep/ .el-table--border::after,
/deep/ .el-table--group::after,
/deep/ .el-table::before {
  background-color: unset;
}
/deep/
  .el-table--striped
  .el-table__body
  tr.el-table__row--striped
  td.el-table__cell {
  background: rgba(0, 70, 176, 0.5);
  background-color: rgba(0, 70, 176, 0.5) !important;
}
/deep/
  .el-table--striped
  .el-table--enable-row-hover
  .el-table__body
  tr:hover
  > td.el-table__cell {
  background: rgba(0, 70, 176, 0.5);
}
/deep/
  .el-table--enable-row-hover
  .el-table__body
  tr:hover
  > td.el-table__cell {
  background-color: unset;
}
/**树形**/
.el-tree {
  background: #fff0;
  color: #fff;
  padding: 0.2rem;
  overflow: scroll;
}
.tree /deep/ .expanded.el-tree-node__expand-icon {
  color: #fff !important;
}
.tree /deep/ .el-tree-node:focus > .el-tree-node__content,
.tree /deep/ .el-tree-node__content:hover,
.el-upload-list__item:hover {
  background-color: rgba(0, 70, 176, 0.2) !important;
}
/deep/ .el-input__prefix {
  right: 5px;
  left: unset;
}
/deep/ .el-input__inner {
  color: #fff;
}
/deep/ .el-input__icon {
  color: #00b4fe;
  font-size: 18px;
}

.normalBk {
  background: rgba(0, 54, 129, 1);
  color: #ffffff;
  cursor: pointer;
}

.searchBth {
  background: #0066ff;
  color: #fff;
  border-radius: 0.05rem;
  padding: 0 0.47rem;
  border: none;
  cursor: pointer;
  height: 35px;
  margin-left: 0.2rem;
}
.tabAll {
  display: flex;
  div:first-child {
    border-radius: 0.04rem 0 0 0.04rem;
  }
  div:last-child {
    border-radius: 0rem 0.04rem 0.04rem 0;
  }
  div {
    font-size: 0.17rem;
    padding: 0.1rem 0.3rem;
    border-radius: 0.04rem 0.04rem 0 0;
    display: flex;
    align-items: center;
    justify-content: center;
    border-radius: 0;
    margin: 0 0.02rem;
    .normalBk();
  }
}
.isActive {
  background: #0056dc !important;
}
</style>